// 搜索页面接口

// 导入网络请求模块
import request from '../util/request'

// 删除用户搜索历史 接口
export function removeHistories (params) {
  return request({
    url: '/v1_0/search/histories',
    method: 'DELETE',
    needToken: true
  })
}

// 获取搜索结果接口

export function getData (params) {
  return request({
    url: 'v1_0/search',
    method: 'GET',
    needToken: true,
    params: params
  })
}

// 获取用户搜索历史
export function getHistories (params) {
  return request({
    url: 'v1_0/search/histories',
    method: 'GET',
    needToken: true,
    params
  })
}

// 获取联想建议（自动补全） 接口
export function getLenovo (q) {
  return request({
    url: 'v1_0/suggestion',
    method: 'GET',
    params: {
      q: q
    }
  })
}
